	module.exports = {"data":{"markdownRemark":{"html":"<!---\nThis is an autogenerated file!\n\nPlease do not edit this file directly, but instead make changes to the\nsigs.yaml file in the project root.\n\nTo understand how this file is generated, see https://git.k8s.io/community/generator/README.md\n-->\n<h1>Testing Special Interest Group</h1>\n<p>Interested in how we can most effectively test Kubernetes. We're interested specifically in making it easier for the community to run tests and contribute test results, to ensure Kubernetes is stable across a variety of cluster configurations and cloud providers.</p>\n<h2>Meetings</h2>\n<ul>\n<li>\n<p>Regular SIG Meeting: <a href=\"https://zoom.us/my/k8s.sig.testing\">Tuesdays at 13:00 PT (Pacific Time)</a> (weekly). <a href=\"http://www.thetimezoneconverter.com/?t=13:00&#x26;tz=PT%20%28Pacific%20Time%29\">Convert to your timezone</a>.</p>\n<ul>\n<li><a href=\"https://bit.ly/k8s-sig-testing-notes\">Meeting notes and Agenda</a>.</li>\n<li><a href=\"https://bit.ly/k8s-sig-testing-videos\">Meeting recordings</a>.</li>\n</ul>\n</li>\n</ul>\n<h2>Leadership</h2>\n<h3>Chairs</h3>\n<p>The Chairs of the SIG run operations and processes governing the SIG.</p>\n<ul>\n<li>Aaron Crickenberger (<strong><a href=\"https://github.com/spiffxp\">@spiffxp</a></strong>)</li>\n<li>Erick Feja (<strong><a href=\"https://github.com/fejta\">@fejta</a></strong>), Google</li>\n<li>Steve Kuznetsov (<strong><a href=\"https://github.com/stevekuznetsov\">@stevekuznetsov</a></strong>), Red Hat</li>\n<li>Timothy St. Clair (<strong><a href=\"https://github.com/timothysc\">@timothysc</a></strong>), Heptio</li>\n</ul>\n<h2>Contact</h2>\n<ul>\n<li><a href=\"https://kubernetes.slack.com/messages/sig-testing\">Slack</a></li>\n<li><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-testing\">Mailing list</a></li>\n<li><a href=\"https://github.com/kubernetes/community/labels/sig%2Ftesting\">Open Community Issues/PRs</a></li>\n</ul>\n<h2>Subprojects</h2>\n<p>The following subprojects are owned by sig-testing:</p>\n<ul>\n<li>\n<p><strong>test-infra</strong></p>\n<ul>\n<li>Owners:</li>\n<li><a href=\"https://raw.githubusercontent.com/kubernetes/test-infra/master/OWNERS\">https://raw.githubusercontent.com/kubernetes/test-infra/master/OWNERS</a></li>\n</ul>\n</li>\n<li>\n<p><strong>repo-publishing</strong></p>\n<ul>\n<li>Owners:</li>\n<li><a href=\"https://raw.githubusercontent.com/kubernetes/publishing-bot/master/OWNERS\">https://raw.githubusercontent.com/kubernetes/publishing-bot/master/OWNERS</a></li>\n</ul>\n</li>\n<li>\n<p><strong>testing-commons</strong></p>\n<ul>\n<li>Description: The Testing Commons is a subproject within the Kubernetes sig-testing community interested code structure, layout, and execution of common test code used throughout the kubernetes project.</li>\n<li>Owners:</li>\n<li><a href=\"https://raw.githubusercontent.com/kubernetes-sig-testing/frameworks/master/OWNERS\">https://raw.githubusercontent.com/kubernetes-sig-testing/frameworks/master/OWNERS</a></li>\n<li><a href=\"https://raw.githubusercontent.com/kubernetes/kubernetes/master/test/OWNERS\">https://raw.githubusercontent.com/kubernetes/kubernetes/master/test/OWNERS</a></li>\n<li>Meetings:</li>\n<li>\n<p>Testing Commons: <a href=\"https://zoom.us/my/k8s.sig.testing\">Wednesdays at 07:30 PT (Pacific Time)</a> (bi-weekly). <a href=\"http://www.thetimezoneconverter.com/?t=07:30&#x26;tz=PT%20%28Pacific%20Time%29\">Convert to your timezone</a>.</p>\n<ul>\n<li><a href=\"https://docs.google.com/document/d/1TOC8vnmlkWw6HRNHoe5xSv5-qv7LelX6XK3UVCHuwb0/edit#heading=h.tnoevy5f439o\">Meeting notes and Agenda</a>.</li>\n</ul>\n</li>\n</ul>\n</li>\n</ul>\n<h2>GitHub Teams</h2>\n<p>The below teams can be mentioned on issues and PRs in order to get attention from the right people.\nNote that the links to display team membership will only work if you are a member of the org.</p>\n<p>The google groups contain the archive of Github team notifications.\nMentioning a team on Github will CC its group.\nMonitor these for Github activity if you are not a member of the team.</p>\n<table>\n<thead>\n<tr>\n<th>Team Name</th>\n<th align=\"center\">Details</th>\n<th align=\"center\">Google Groups</th>\n<th>Description</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>@kubernetes/sig-testing-api-reviews</td>\n<td align=\"center\"><a href=\"https://github.com/orgs/kubernetes/teams/sig-testing-api-reviews\">link</a></td>\n<td align=\"center\"><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-testing-api-reviews\">link</a></td>\n<td>API Changes and Reviews</td>\n</tr>\n<tr>\n<td>@kubernetes/sig-testing-bugs</td>\n<td align=\"center\"><a href=\"https://github.com/orgs/kubernetes/teams/sig-testing-bugs\">link</a></td>\n<td align=\"center\"><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-testing-bugs\">link</a></td>\n<td>Bug Triage and Troubleshooting</td>\n</tr>\n<tr>\n<td>@kubernetes/sig-testing-feature-requests</td>\n<td align=\"center\"><a href=\"https://github.com/orgs/kubernetes/teams/sig-testing-feature-requests\">link</a></td>\n<td align=\"center\"><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-testing-feature-requests\">link</a></td>\n<td>Feature Requests</td>\n</tr>\n<tr>\n<td>@kubernetes/sig-testing-misc</td>\n<td align=\"center\"><a href=\"https://github.com/orgs/kubernetes/teams/sig-testing-misc\">link</a></td>\n<td align=\"center\"><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-testing-misc\">link</a></td>\n<td>General Discussion</td>\n</tr>\n<tr>\n<td>@kubernetes/sig-testing-pr-reviews</td>\n<td align=\"center\"><a href=\"https://github.com/orgs/kubernetes/teams/sig-testing-pr-reviews\">link</a></td>\n<td align=\"center\"><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-testing-pr-reviews\">link</a></td>\n<td>PR Reviews</td>\n</tr>\n<tr>\n<td>@kubernetes/sig-testing-proposals</td>\n<td align=\"center\"><a href=\"https://github.com/orgs/kubernetes/teams/sig-testing-proposals\">link</a></td>\n<td align=\"center\"><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-testing-proposals\">link</a></td>\n<td>Design Proposals</td>\n</tr>\n<tr>\n<td>@kubernetes/sig-testing-test-failures</td>\n<td align=\"center\"><a href=\"https://github.com/orgs/kubernetes/teams/sig-testing-test-failures\">link</a></td>\n<td align=\"center\"><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-testing-test-failures\">link</a></td>\n<td>Test Failures and Triage</td>\n</tr>\n</tbody>\n</table>\n<!-- BEGIN CUSTOM CONTENT -->\n<!-- END CUSTOM CONTENT -->"},"site":{"siteMetadata":{"sigs":["sig-api-machinery","sig-apps","sig-architecture","sig-auth","sig-autoscaling","sig-aws","sig-azure","sig-big-data","sig-cli","sig-cloud-provider","sig-cluster-lifecycle","sig-cluster-ops","sig-contributor-experience","sig-docs","sig-gcp","sig-ibmcloud","sig-instrumentation","sig-multicluster","sig-network","sig-node","sig-openstack","sig-product-management","sig-release","sig-scalability","sig-scheduling","sig-service-catalog","sig-storage","sig-testing","sig-ui","sig-vmware","sig-windows"]}}},"pathContext":{"slug":"/sig-testing/"}}

